.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1a1a1a;
  padding: 20px;
  position: relative;
}

.login-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(255, 165, 0, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 165, 0, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.login-card {
  background: #2a2a2a;
  border: 4px solid #ffa500;
  box-shadow: 8px 8px 0px #cc8400, 16px 16px 0px #996600;
  padding: 40px;
  width: 100%;
  max-width: 400px;
  position: relative;
  z-index: 1;
}

.login-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.05) 50%, transparent 52%);
  pointer-events: none;
}

.login-header {
  text-align: center;
  margin-bottom: 30px;
  position: relative;
  z-index: 2;
}

.login-header h1 {
  color: #ffffff;
  font-size: 28px;
  margin-bottom: 8px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 3px 3px 0px #000000;
  font-family: 'Courier New', monospace;
}

.login-header p {
  color: #cccccc;
  font-size: 16px;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.error-message {
  background: #cc0000;
  color: #ffffff;
  padding: 12px;
  margin-bottom: 20px;
  border: 3px solid #cc0000;
  box-shadow: 4px 4px 0px #990000;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  font-family: 'Courier New', monospace;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 25px;
  position: relative;
  z-index: 2;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form-group label {
  color: #ffffff;
  font-weight: bold;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.form-group input {
  background: #1a1a1a;
  border: 3px solid #ffa500;
  color: #ffffff;
  font-family: 'Courier New', monospace;
  padding: 12px 16px;
  font-size: 16px;
  box-shadow: inset 3px 3px 0px #0a0a0a;
  outline: none;
  transition: all 0.1s ease;
}

.form-group input:focus {
  border-color: #ffcc00;
  box-shadow: inset 3px 3px 0px #0a0a0a, 0 0 0 2px rgba(255, 204, 0, 0.3);
}

.form-group input::placeholder {
  color: #888888;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.login-button {
  background: #ffa500;
  color: #ffffff;
  border: 4px solid #ffa500;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  transition: all 0.1s ease;
  margin-top: 10px;
  box-shadow: 6px 6px 0px #cc8400;
  font-family: 'Courier New', monospace;
  position: relative;
}

.login-button:hover:not(:disabled) {
  transform: translate(3px, 3px);
  box-shadow: 3px 3px 0px #cc8400;
}

.login-button:active:not(:disabled) {
  transform: translate(6px, 6px);
  box-shadow: 0px 0px 0px #cc8400;
}

.login-button:disabled {
  background: #666666;
  border-color: #666666;
  color: #999999;
  cursor: not-allowed;
  transform: none;
  box-shadow: 6px 6px 0px #444444;
}

.login-footer {
  margin-top: 30px;
  text-align: center;
  padding-top: 20px;
  border-top: 3px solid #ffa500;
  position: relative;
  z-index: 2;
}

.login-footer p {
  color: #cccccc;
  font-size: 14px;
  margin: 0;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.login-footer code {
  background: #1a1a1a;
  color: #ffa500;
  padding: 4px 8px;
  border: 2px solid #ffa500;
  box-shadow: 2px 2px 0px #cc8400;
  font-family: 'Courier New', monospace;
  font-weight: bold;
  text-transform: uppercase;
}

@media (max-width: 480px) {
  .login-card {
    padding: 30px 20px;
    margin: 10px;
  }
  
  .login-header h1 {
    font-size: 24px;
    letter-spacing: 2px;
  }
  
  .login-button {
    padding: 14px 24px;
    font-size: 14px;
  }
}
