.admin-container {
  min-height: 100vh;
  background: #1a1a1a;
  padding: 20px;
  position: relative;
}

.admin-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 15% 15%, rgba(255, 165, 0, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 85% 85%, rgba(255, 165, 0, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #2a2a2a;
  border: 4px solid #ffa500;
  box-shadow: 6px 6px 0px #cc8400;
  padding: 20px 30px;
  margin-bottom: 30px;
  flex-wrap: wrap;
  gap: 15px;
  position: relative;
  z-index: 1;
}

.admin-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.05) 50%, transparent 52%);
  pointer-events: none;
}

.admin-header h1 {
  color: #ffffff;
  font-size: 28px;
  margin: 0;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 3px 3px 0px #000000;
  font-family: 'Courier New', monospace;
  position: relative;
  z-index: 2;
}

.admin-info {
  display: flex;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
  position: relative;
  z-index: 2;
}

.admin-info span {
  color: #cccccc;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.logout-btn {
  background: #cc0000;
  color: #ffffff;
  border: 3px solid #cc0000;
  padding: 8px 16px;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #990000;
  font-family: 'Courier New', monospace;
}

.logout-btn:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #990000;
}

.logout-btn:active {
  transform: translate(4px, 4px);
  box-shadow: 0px 0px 0px #990000;
}

.admin-content {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.stats-card {
  background: #2a2a2a;
  border: 4px solid #ffa500;
  box-shadow: 6px 6px 0px #cc8400;
  padding: 30px;
  margin-bottom: 30px;
  position: relative;
}

.stats-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.05) 50%, transparent 52%);
  pointer-events: none;
}

.stats-card h2 {
  color: #ffffff;
  font-size: 24px;
  margin-bottom: 20px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0px #000000;
  font-family: 'Courier New', monospace;
  position: relative;
  z-index: 2;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  position: relative;
  z-index: 2;
}

.stat-item {
  text-align: center;
  padding: 20px;
  background: #1a1a1a;
  border: 3px solid #ffa500;
  box-shadow: 4px 4px 0px #cc8400;
  position: relative;
}

.stat-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.1) 50%, transparent 52%);
  pointer-events: none;
}

.stat-number {
  display: block;
  font-size: 32px;
  font-weight: bold;
  color: #ffa500;
  margin-bottom: 8px;
  text-shadow: 2px 2px 0px #000000;
  font-family: 'Courier New', monospace;
  position: relative;
  z-index: 2;
}

.stat-label {
  color: #cccccc;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
  position: relative;
  z-index: 2;
}

.reports-section {
  background: #2a2a2a;
  border: 4px solid #ffa500;
  box-shadow: 6px 6px 0px #cc8400;
  padding: 30px;
  position: relative;
}

.reports-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.05) 50%, transparent 52%);
  pointer-events: none;
}

.reports-section h2 {
  color: #ffffff;
  font-size: 24px;
  margin-bottom: 20px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0px #000000;
  font-family: 'Courier New', monospace;
  position: relative;
  z-index: 2;
}

.no-reports {
  text-align: center;
  padding: 40px;
  color: #cccccc;
  position: relative;
  z-index: 2;
}

.no-reports p {
  font-size: 18px;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.reports-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 20px;
  position: relative;
  z-index: 2;
}

.report-card {
  border: 3px solid #ffa500;
  padding: 20px;
  background: #1a1a1a;
  box-shadow: 4px 4px 0px #cc8400;
  transition: all 0.1s ease;
  position: relative;
}

.report-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.05) 50%, transparent 52%);
  pointer-events: none;
}

.report-card:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #cc8400;
}

.report-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
  flex-wrap: wrap;
  gap: 10px;
  position: relative;
  z-index: 2;
}

.report-header h3 {
  color: #ffffff;
  font-size: 16px;
  margin: 0;
  font-weight: bold;
  flex: 1;
  min-width: 200px;
  word-break: break-word;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.report-actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.view-btn, .download-btn, .delete-btn {
  padding: 6px 12px;
  text-decoration: none;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  border: 2px solid;
  transition: all 0.1s ease;
  font-family: 'Courier New', monospace;
  box-shadow: 2px 2px 0px;
}

.view-btn {
  background: #007bff;
  color: #ffffff;
  border-color: #007bff;
  box-shadow: 2px 2px 0px #0056b3;
}

.view-btn:hover {
  transform: translate(1px, 1px);
  box-shadow: 1px 1px 0px #0056b3;
}

.download-btn {
  background: #00aa00;
  color: #ffffff;
  border-color: #00aa00;
  box-shadow: 2px 2px 0px #006600;
}

.download-btn:hover {
  transform: translate(1px, 1px);
  box-shadow: 1px 1px 0px #006600;
}

.delete-btn {
  background: #cc0000;
  color: #ffffff;
  border-color: #cc0000;
  box-shadow: 2px 2px 0px #990000;
}

.delete-btn:hover {
  transform: translate(1px, 1px);
  box-shadow: 1px 1px 0px #990000;
}

.report-details {
  color: #cccccc;
  font-size: 14px;
  position: relative;
  z-index: 2;
  font-family: 'Courier New', monospace;
}

.report-details p {
  margin: 5px 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  color: #ffffff;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #444444;
  border-top: 4px solid #ffa500;
  border-radius: 0;
  animation: pixelSpin 1s linear infinite;
  box-shadow: 4px 4px 0px #222222;
  margin-bottom: 20px;
}

@keyframes pixelSpin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-message {
  background: #cc0000;
  color: #ffffff;
  padding: 12px 16px;
  border: 3px solid #cc0000;
  box-shadow: 4px 4px 0px #990000;
  font-size: 14px;
  text-align: center;
  margin-bottom: 20px;
  font-weight: bold;
  text-transform: uppercase;
  font-family: 'Courier New', monospace;
}

/* Admin Login Page Styles */
.admin-login-card {
  background: #2a2a2a;
  border: 4px solid #ffa500;
  box-shadow: 8px 8px 0px #cc8400, 16px 16px 0px #996600;
  padding: 40px;
  width: 100%;
  max-width: 400px;
  position: relative;
  z-index: 1;
  margin: 0 auto;
  margin-top: 100px;
}

.admin-login-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.05) 50%, transparent 52%);
  pointer-events: none;
}

.admin-login-header {
  text-align: center;
  margin-bottom: 30px;
  position: relative;
  z-index: 2;
}

.admin-login-header h1 {
  color: #ffffff;
  font-size: 28px;
  margin-bottom: 8px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 3px 3px 0px #000000;
  font-family: 'Courier New', monospace;
}

.admin-login-header p {
  color: #cccccc;
  font-size: 16px;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.admin-login-form {
  display: flex;
  flex-direction: column;
  gap: 25px;
  position: relative;
  z-index: 2;
}

.admin-login-form .form-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.admin-login-form .form-group label {
  color: #ffffff;
  font-weight: bold;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.admin-login-footer {
  margin-top: 30px;
  text-align: center;
  padding-top: 20px;
  border-top: 3px solid #ffa500;
  position: relative;
  z-index: 2;
}

.admin-login-footer p {
  color: #cccccc;
  font-size: 14px;
  margin: 0;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.main-app-link {
  color: #ffa500;
  text-decoration: none;
  font-weight: bold;
  padding: 4px 8px;
  border: 2px solid #ffa500;
  box-shadow: 2px 2px 0px #cc8400;
  transition: all 0.1s ease;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.main-app-link:hover {
  transform: translate(1px, 1px);
  box-shadow: 1px 1px 0px #cc8400;
  background: #ffa500;
  color: #1a1a1a;
}

/* Reports Header with Download All Button */
.reports-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 15px;
}

.download-all-btn {
  background: #00aa00;
  color: #ffffff;
  border: 3px solid #00aa00;
  padding: 10px 20px;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #006600;
  font-family: 'Courier New', monospace;
  font-size: 12px;
}

.download-all-btn:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #006600;
}

.download-all-btn:active {
  transform: translate(4px, 4px);
  box-shadow: 0px 0px 0px #006600;
}

@media (max-width: 768px) {
  .admin-header {
    flex-direction: column;
    text-align: center;
    padding: 20px;
  }
  
  .admin-header h1 {
    font-size: 24px;
    letter-spacing: 2px;
  }
  
  .reports-section {
    padding: 20px;
  }
  
  .reports-grid {
    grid-template-columns: 1fr;
  }
  
  .report-header {
    flex-direction: column;
    align-items: stretch;
  }
  
  .report-header h3 {
    min-width: auto;
    margin-bottom: 10px;
  }
  
  .report-actions {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .admin-container {
    padding: 10px;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .report-actions {
    flex-direction: column;
  }
  
  .view-btn, .download-btn, .delete-btn {
    text-align: center;
  }
}
