.dashboard-container {
  min-height: 100vh;
  background: #1a1a1a;
  padding: 20px;
  position: relative;
}

.dashboard-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 10% 20%, rgba(255, 165, 0, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 90% 80%, rgba(255, 165, 0, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #2a2a2a;
  border: 4px solid #ffa500;
  box-shadow: 6px 6px 0px #cc8400;
  padding: 20px 30px;
  margin-bottom: 30px;
  flex-wrap: wrap;
  gap: 15px;
  position: relative;
  z-index: 1;
}

.dashboard-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.05) 50%, transparent 52%);
  pointer-events: none;
}

.dashboard-header h1 {
  color: #ffffff;
  font-size: 28px;
  margin: 0;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 3px 3px 0px #000000;
  font-family: 'Courier New', monospace;
  position: relative;
  z-index: 2;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
  position: relative;
  z-index: 2;
}

.user-info span {
  color: #cccccc;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.logout-btn {
  background: #cc0000;
  color: #ffffff;
  border: 3px solid #cc0000;
  padding: 8px 16px;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.1s ease;
  box-shadow: 4px 4px 0px #990000;
  font-family: 'Courier New', monospace;
}

.logout-btn:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #990000;
}

.logout-btn:active {
  transform: translate(4px, 4px);
  box-shadow: 0px 0px 0px #990000;
}

.dashboard-content {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.upload-card {
  background: #2a2a2a;
  border: 4px solid #ffa500;
  box-shadow: 8px 8px 0px #cc8400, 16px 16px 0px #996600;
  padding: 40px;
  position: relative;
}

.upload-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.05) 50%, transparent 52%);
  pointer-events: none;
}

.upload-header {
  text-align: center;
  margin-bottom: 30px;
  position: relative;
  z-index: 2;
}

.upload-header h2 {
  color: #ffffff;
  font-size: 24px;
  margin-bottom: 8px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0px #000000;
  font-family: 'Courier New', monospace;
}

.upload-header p {
  color: #cccccc;
  font-size: 16px;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.upload-form {
  display: flex;
  flex-direction: column;
  gap: 25px;
  position: relative;
  z-index: 2;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form-group label {
  color: #ffffff;
  font-weight: bold;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.form-group input[type="text"] {
  background: #1a1a1a;
  border: 3px solid #ffa500;
  color: #ffffff;
  font-family: 'Courier New', monospace;
  padding: 12px 16px;
  font-size: 16px;
  box-shadow: inset 3px 3px 0px #0a0a0a;
  outline: none;
  transition: all 0.1s ease;
}

.form-group input[type="text"]:focus {
  border-color: #ffcc00;
  box-shadow: inset 3px 3px 0px #0a0a0a, 0 0 0 2px rgba(255, 204, 0, 0.3);
}

.form-group input[type="text"]:disabled {
  background: #333333;
  cursor: not-allowed;
  color: #666666;
}

.file-input-container {
  position: relative;
}

.file-input-container input[type="file"] {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.file-input-label {
  display: block;
  padding: 12px 16px;
  border: 3px dashed #ffa500;
  text-align: center;
  cursor: pointer;
  transition: all 0.1s ease;
  background: #1a1a1a;
  color: #cccccc;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
  box-shadow: inset 2px 2px 0px #0a0a0a;
}

.file-input-label:hover {
  border-color: #ffcc00;
  background: #222222;
  color: #ffcc00;
}

.file-input-container input[type="file"]:disabled + .file-input-label {
  background: #333333;
  cursor: not-allowed;
  color: #666666;
  border-color: #666666;
}

.form-group small {
  color: #888888;
  font-size: 12px;
  margin-top: 4px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: 'Courier New', monospace;
}

.error-message {
  background: #cc0000;
  color: #ffffff;
  padding: 12px 16px;
  border: 3px solid #cc0000;
  box-shadow: 4px 4px 0px #990000;
  font-size: 14px;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  font-family: 'Courier New', monospace;
}

.success-message {
  background: #00aa00;
  color: #ffffff;
  padding: 12px 16px;
  border: 3px solid #00aa00;
  box-shadow: 4px 4px 0px #006600;
  font-size: 14px;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  font-family: 'Courier New', monospace;
}

.submit-button {
  background: #ffa500;
  color: #ffffff;
  border: 4px solid #ffa500;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  transition: all 0.1s ease;
  margin-top: 10px;
  align-self: center;
  min-width: 200px;
  box-shadow: 6px 6px 0px #cc8400;
  font-family: 'Courier New', monospace;
}

.submit-button:hover:not(:disabled) {
  transform: translate(3px, 3px);
  box-shadow: 3px 3px 0px #cc8400;
}

.submit-button:active:not(:disabled) {
  transform: translate(6px, 6px);
  box-shadow: 0px 0px 0px #cc8400;
}

.submit-button:disabled {
  background: #666666;
  border-color: #666666;
  color: #999999;
  cursor: not-allowed;
  transform: none;
  box-shadow: 6px 6px 0px #444444;
}

.upload-info {
  margin-top: 40px;
  padding-top: 30px;
  border-top: 3px solid #ffa500;
  position: relative;
  z-index: 2;
}

.upload-info h3 {
  color: #ffffff;
  font-size: 18px;
  margin-bottom: 15px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0px #000000;
  font-family: 'Courier New', monospace;
}

.upload-info ul {
  color: #cccccc;
  line-height: 1.6;
  margin: 0;
  padding-left: 20px;
  font-family: 'Courier New', monospace;
}

.upload-info li {
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

@media (max-width: 768px) {
  .dashboard-header {
    flex-direction: column;
    text-align: center;
    padding: 20px;
  }
  
  .dashboard-header h1 {
    font-size: 24px;
    letter-spacing: 2px;
  }
  
  .upload-card {
    padding: 30px 20px;
    margin: 0 10px;
  }
  
  .user-info {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .dashboard-container {
    padding: 10px;
  }
  
  .upload-card {
    padding: 20px 15px;
  }
  
  .submit-button {
    min-width: 100%;
  }
}
