* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Courier New', monospace;
  background: #1a1a1a;
  color: #ffffff;
  line-height: 1.4;
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
}

.App {
  min-height: 100vh;
  background: #1a1a1a;
}

/* Pixel Art Button Styles */
.pixel-button {
  background: #ffa500;
  border: 3px solid #ffa500;
  color: #ffffff;
  font-family: 'Courier New', monospace;
  font-weight: bold;
  text-transform: uppercase;
  padding: 12px 24px;
  cursor: pointer;
  position: relative;
  box-shadow: 4px 4px 0px #cc8400;
  transition: all 0.1s ease;
  font-size: 14px;
  letter-spacing: 1px;
}

.pixel-button:hover {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0px #cc8400;
}

.pixel-button:active {
  transform: translate(4px, 4px);
  box-shadow: 0px 0px 0px #cc8400;
}

.pixel-button:disabled {
  background: #666666;
  border-color: #666666;
  color: #999999;
  cursor: not-allowed;
  transform: none;
  box-shadow: 4px 4px 0px #444444;
}

/* Pixel Art Input Styles */
.pixel-input {
  background: #2a2a2a;
  border: 3px solid #ffa500;
  color: #ffffff;
  font-family: 'Courier New', monospace;
  padding: 12px 16px;
  font-size: 14px;
  box-shadow: inset 2px 2px 0px #1a1a1a;
  outline: none;
  transition: all 0.1s ease;
}

.pixel-input:focus {
  border-color: #ffcc00;
  box-shadow: inset 2px 2px 0px #1a1a1a, 0 0 0 2px rgba(255, 204, 0, 0.3);
}

.pixel-input::placeholder {
  color: #888888;
}

/* Pixel Art Card Styles */
.pixel-card {
  background: #2a2a2a;
  border: 3px solid #ffa500;
  box-shadow: 6px 6px 0px #cc8400;
  position: relative;
}

.pixel-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 48%, rgba(255, 165, 0, 0.1) 50%, transparent 52%);
  pointer-events: none;
}

/* Pixel Art Text Styles */
.pixel-title {
  color: #ffffff;
  font-family: 'Courier New', monospace;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0px #000000;
}

.pixel-subtitle {
  color: #cccccc;
  font-family: 'Courier New', monospace;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Difficulty Badges */
.difficulty-easy {
  background: #00aa00;
  color: #ffffff;
  border: 2px solid #00aa00;
  padding: 4px 8px;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 2px 2px 0px #006600;
}

.difficulty-medium {
  background: #ff8800;
  color: #ffffff;
  border: 2px solid #ff8800;
  padding: 4px 8px;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 2px 2px 0px #cc6600;
}

.difficulty-hard {
  background: #cc0000;
  color: #ffffff;
  border: 2px solid #cc0000;
  padding: 4px 8px;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 2px 2px 0px #990000;
}

/* Points Badge */
.points-badge {
  background: #2a2a2a;
  color: #ffffff;
  border: 2px solid #ffa500;
  padding: 4px 8px;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 2px 2px 0px #cc8400;
}

/* Progress Bar */
.pixel-progress {
  background: #444444;
  border: 2px solid #666666;
  height: 20px;
  position: relative;
  box-shadow: inset 2px 2px 0px #222222;
}

.pixel-progress-fill {
  background: #ffa500;
  height: 100%;
  position: relative;
  box-shadow: inset 0 0 0 1px #cc8400;
}

.pixel-progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent 0%, rgba(255, 255, 255, 0.2) 50%, transparent 100%);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Error and Success Messages */
.pixel-error {
  background: #cc0000;
  color: #ffffff;
  border: 3px solid #cc0000;
  padding: 12px 16px;
  font-family: 'Courier New', monospace;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 4px 4px 0px #990000;
  text-align: center;
}

.pixel-success {
  background: #00aa00;
  color: #ffffff;
  border: 3px solid #00aa00;
  padding: 12px 16px;
  font-family: 'Courier New', monospace;
  font-weight: bold;
  text-transform: uppercase;
  box-shadow: 4px 4px 0px #006600;
  text-align: center;
}

/* Loading Spinner */
.pixel-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #444444;
  border-top: 4px solid #ffa500;
  border-radius: 0;
  animation: pixelSpin 1s linear infinite;
  box-shadow: 4px 4px 0px #222222;
}

@keyframes pixelSpin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive Design */
@media (max-width: 768px) {
  .pixel-button {
    padding: 10px 20px;
    font-size: 12px;
  }
  
  .pixel-input {
    padding: 10px 12px;
    font-size: 12px;
  }
  
  .pixel-title {
    font-size: 18px;
    letter-spacing: 1px;
  }
}
